#tracks {
  padding-top: 150px;
  padding-bottom: 50px;
  background-color: #0b1014;
}

.wrapper {
  position: relative;
  display: flex;
  width: 100vw;
  height: 400px;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease;
}

@media screen and (max-width: 767px) {
  .wrapper { height: 800px; }
}

.content {
  display: flex;
  max-width: 1024px;
  width: 100%;
  margin-top: 200px;
  margin-bottom: 100px;
  margin: 0 auto;
  padding: 0 4%;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 767px) {
  .content {
    height: 500px;
    padding-top: 50px;
    flex-direction: row;
    flex-flow: row wrap;
  }
}

.card {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 300px;
  min-width: 220px;
  height: 240px;
  margin: 10px;
  font-size: 16px;
  background-color: #0b1014;
  box-shadow: 5px 7px 10px rgba(110,255,241,0.7);
  border: 2px solid rgba(7,7,7,0.12);
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s ease;
}

.icon {
  display: flex;
  width: 100%;
  max-width: 80px;
  height: 80px;
  margin: 0 auto;
  color: white;
  background: linear-gradient(90deg, #ff7e7e 0%, #ff4848 40%, rgba(0,0,0,0.28) 60%);
  background-position: 0px;
  background-size: 200px;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  transition: all 0.8s ease;

}

.card .title {
  width: 100%;
  margin: 0;
  margin-top: 30px;
  font-weight: 600;
  letter-spacing: 3px;
  color: white;
  text-align: center;
  text-transform: uppercase;

}

.card .text {
  width: 80%;
  max-height: 0;
  margin: 0 auto;
  margin-top: 20px;
  font-size: 14px;
  font-weight: 200;
  text-align: center;
  color: white;
  opacity: 0;
  transition: all 0.3s ease;
}

.card:hover { height: 345px; }

.card:hover .info { height: 80%; }

.card:hover .icon {
  background-position: -120px;
  transition: all 0.3s ease;
}

.card:hover .icon i {
  background: linear-gradient(90deg, #ff7e7e, #ff4848);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  opacity: 1;
  transition: all 0.3s ease;
}

.card:hover .text {
  max-height: 40px;
  transition: all 0.3s ease;
  opacity: 1;
}
